* {
  margin: 0;
}
body {
  font-family: "Epilogue", sans-serif;
  position: relative;
}

.navb {
  display: flex;
  justify-content: space-between;
}
.logo,
.iconmenu {
  padding:25px 30px;

}

.iconmenu {
  display: flex;
  flex-direction: row-reverse;
  /* transition: 10s; */
}

.register{
  border: 2px solid black;
  padding: 5px 20px !important;
  border-radius: 10px;
}
.iconmenu img:hover,
.register:hover{
  cursor: pointer;
}

.menuimg{
  transition: 10s;
}
.open {
  position: absolute;
  width: 65%;
  right: 0;
  background-color: white;
  z-index: 99;
 height: 100%;
  box-shadow: -200px -5px 1px hsla(0, 0%, 22%, 0.77);
  color: hsl(0, 0%, 41%);
}

.moreoptions{
  display: none;
  
}

.menufeat{
  padding: 20px;
}

.log-reg{
  display: none;
}

.log-reg2{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.log-reg2>div{
  padding: 20px 0;
}

.menufeat>div{
  padding: 10px;
  font-size: 18px;
  
}

.menufeat>div div img{
  margin-left: 8px;
}


.menufeat>div:hover {
  color:black;
  cursor: pointer;
}

.menufeat>div ul{
 padding-top: 5px;
}
.menufeat>div ul li{
  list-style-type: none;
  font-size: 18px;
  padding: 10px 0 ;
}


.main-section{
  display: inline-flex;
  flex-direction: column-reverse;

}

.picture-container{
  margin-top: 10%;
}

.heroTxt {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10%;
}
.heroTxt h2 {
  font-weight: 700;
  /* font-size:50px; */
  text-align: center;
  font-size: 30px;
}

.heroTxt p {
  color: hsl(0, 0%, 41%);
  font-size: 18px;
  line-height: 30px;
  text-align: center;
}
picture img {
  width: 100%;
}

.learn{
  border: 2px solid black;
  padding: 13px 25px;
  border-radius: 10px;
  background: black;
  font-size: 18px;
  transition: .5s;
  color: white;
  margin-top: 8%;
}

.learn:hover{
  background-color: inherit;
  cursor: pointer;
  color: black;
}

.img-cont {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 30%;
  margin-bottom: 10px;
}

.img-cont img{
  width: 20%;
}

@media (min-width: 1024px) {
  .navb {
    align-items: center;
  }
  .iconmenu{
    display: none;
  }
  .log-reg{
    display: flex;
  }

  .log-reg2{
    display: none;
  }

  .moreoptions{
    display: block; 
  }

  .open {
    position: relative;
    width: auto;
    z-index: 1;
    box-shadow: none;
  }
  .deskview{
    position: relative;
    width: 100%;
    height: 100%;
  }

  .menufeat,
  #nemenu{
    display: flex;
    align-items: center;
    font-size: 14px;
  }

  .logo,
  .menufeat {
  padding:20px 30px;

}
  #nemenu{
    justify-content: space-between;
    /* align-items: center; */
  }

  .menufeat>div{
    padding: 0 10px;
    font-size: 16px;
    position: relative;
  }

  .menufeat>div ul{
    position: absolute;
    right: 0;
    border-radius: 5px;
    background-color: white;
    border: 1px solid #ddd;
    padding: 10px;
    font-size: 14px !important;
    box-shadow: 5px 5px 5px #ddd;
    width: 105%;
   }

   .menufeat>div ul li{
    font-size: 13.5px;
    text-align: center;
   }

   .menufeat>div ul li img{
    margin-right: 3px;
   }

   .register{
    margin-left: 10px;
   }
   .main-section{
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    margin: 0 8%;
    margin-top: 2%;
  }

  .heroTxt {
    margin: 0 ;
    width: 50%;
    align-items: left;
    flex-direction: column;
  justify-content:start;
  align-items: center;
  }
  
 
  .heroTxt h2 {
    font-weight: 700;
    font-size:85px;
    text-align: left;
    width: 100%;
  }
  
  .heroTxt p {
    width: 100%;
    font-size: 20px;
    text-align: left;
  }
  .picture-container{
    margin-top: 0;
    width: 50%;
    
  }

  picture{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  .picture-container img {
   width: 60%;
   height: 100%;
  }
  .learn-container{
    width: 100%;
  }

  .learn{
    display: inline-block;
    margin-top: 5%;
    font-size: 16px;
    padding: 10px 20px;
  }
  .img-cont {
    width: 100%;
    margin-top: 8%;
    margin-bottom: 0;
  }
}
